import React, { Component } from 'react';
import './App.css';
import './nprogress.css';
import EventGenre from './EventGenre';
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import WelcomeScreen from './WelcomeScreen';
import 'bootstrap/dist/css/bootstrap.min.css';
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

import { getEvents, extractLocations, checkToken, getAccessToken } from './api';
import { mockData } from './mock-data';

class App extends Component {

  state = {
    events: [],
    locations: [],
    showWelcomeScreen: undefined,
    locationSelected: 'all',
    numberOfEvents: 12
  };

  async componentDidMount() {
    this.mounted = true;
    const accessToken = localStorage.getItem('access_token');
    const isTokenValid = (await checkToken(accessToken)).error ? false : true;
    const searchParams = new URLSearchParams(window.location.search);
    const code = searchParams.get('code');
    this.setState({ showWelcomeScreen: !(code || isTokenValid) });
    if ((code || isTokenValid) && this.mounted) {
      getEvents().then((events) => {
        if (this.mounted) {
          this.setState({
            events: events,
            locations: extractLocations(events),
          });
        }
      });
    }
  }

  getData = () => {
    const { locations, events } = this.state;
    const data = locations.map((location) => {
      const chartNumber = events.filter(
        (event) => event.location === location
      ).length;
      const city = location.split(', ').shift();
      return { city, chartNumber };
    });
    console.log(data);
    return data;
  };

  componentWillUnmount() {
    this.mounted = false;
  }

  updateEvents = (location) => {
    getEvents().then((events) => {
      const locationEvents =
        location === 'all'
          ? events
          : events.filter((event) => event.location === location);

      if (this.mounted) {
        this.setState({
          events: locationEvents,
        });
      }
    });
  };

  updateNumber = (number) => {
    let numberWarning = '';
    if (number < 0) {
      numberWarning = 'Number can not be negative.';
    } else if (number > this.state.events.length) {
      numberWarning = `There are only ${this.state.events.length} events.`;
      this.setState({ number: this.state.events.length });
    } else {
      this.setState({ number: number });
    }
    this.setState({ errorText: numberWarning });
  };

  render() {
    if (this.state.showWelcomeScreen === undefined)
      return <div className="App" />;
  return (
    <section className="layout">
      <div className="sidebar">
        <p className="marquee">HAPPENINGS</p>
      </div>
      <div className="body">
        <div className="comp-1">Use this progressive web application (PWA) to search for events happening in your city! It connects to the Google Calendar API to fetch events for a city as specified by the you!</div>
        <div className="comp-1">
        </div>

        <div className="comp-1">
          <CitySearch updateEvents={this.updateEvents} locations={this.state.locations} />
        </div>
        <div className="comp-1">
        <NumberOfEvents updateEvents={this.updateEvents} numberOfEvents={this.state.numberOfEvents} />
        </div>
        <div className="comp-1">
        <p className="p2">Visualising events by city</p>
        <ResponsiveContainer height={400} >
            <ScatterChart margin={{ top: 20, right: 20, bottom: 20, left: 20 }}>
              <CartesianGrid />
              <XAxis type="category" dataKey="city" name="city" />
              <YAxis
                allowDecimals={false}
                type="number"
                dataKey="number"
                name="number of events"/>
              <Tooltip cursor={{ strokeDasharray: "3 3" }} />
              <Scatter data={this.getData()} fill="#8884d8" />
            </ScatterChart>
        </ResponsiveContainer>
        <p className="p2">Visualising events by genre</p>
        <EventGenre events={this.state.events} />
        </div>
        <div className="comp-1">
          <EventList events={this.state.events} />
        </div>
        <WelcomeScreen
          showWelcomeScreen={this.state.showWelcomeScreen}
          getAccessToken={() => {
            getAccessToken();
          }}
        />
      </div>
    </section> 
  );
}
}

export default App;